<template>
    <div class="officalInfo">
        <div class="seeCarHeader">
            <div class="seeCarHeader_content">
                <span class="return" @click="back"><img src="../../../../assets/return.png" width="100%" alt=""></span>
                <span class="title">官方活动通知</span>
                <span class="menu" @click="showMenu('搜索')"><img src="../../../../assets/menu.png" width="100%"
                                                                alt=""></span>
            </div>
        </div>
        <div class="officalContent">
            <div class="officalInfoItem">
                <li v-for="msgInfo in msgInfos">
                    <div v-if="msgInfo.image_url != null&&msgInfo.image_url != ''" class="hasImg">
                        <p class="time"><span>{{msgInfo.create_time}}</span></p>
                        <div class="offContent">
                            <p class="msg_title">{{msgInfo.title_rule}}</p>
                            <p class="msg_info">{{msgInfo.msg}}</p>
                            <a class="picHeight" :href="msgInfo.url"><img width="100%" height="100%"
                                                                          :src="msgInfo.image_url"></a>
                            <p class="to_msgDetail" @click="toMsgDetail(msgInfo.url)">
                                <a href="javascript:;">查看详情</a>
                                <i class="icon iconfont icon-jiankuohaoxiyou"></i>
                            </p>
                        </div>
                    </div>
                    <div v-else class="noImg">
                        <p class="time"><span>{{msgInfo.create_time}}</span></p>
                        <div class="noImgContent">
                            <p class="title">{{msgInfo.title_rule}}</p>
                            <p class="Content">{{msgInfo.msg}}</p>
                        </div>
                    </div>
                </li>
            </div>
            <!--<div class="noCarImg"><img src="../message@2x.png" alt=""></div>-->
        </div>
        <menuPage ref="menuMark" @close="closeThis"></menuPage>
    </div>
</template>
<script>
    import menuPage from "../../../public/nav.vue"

    export default {
        data() {
            return {
                msgType: 5,
                noCarImg: true,
                title: '',
                msgInfos: '',
                tokens: '',
                nativeHref: ''
            }
        },
        mounted() {
            this.tokens = JSON.parse(window.localStorage.getItem("user")).token;
            var _this = this;
            $.ajax({
                type: "POST",
                url: this.global.host + 'message/getMessageList/' + _this.msgType + '/' + 1 + '?token=' + _this.tokens,
                success: function (response) {
                    if (response.resultCode == '10086') {
                        _this.errorAjax(response.resultMsg, _this)
                    } else {
                        if (response.resultCode == "0000") {
                            _this.msgInfos = response.result.rows;
                        }
                        if (response.resultCode == '1111') {

                        }
                    }
                },
                error: function (response) {

                }
            });
        },
        methods: {
            back() {
                this.$router.back(-1)
            },
            closeThis() {
                this.showThis = false;
            },
            showMenu(msg) {
                this.$refs.menuMark.show(msg, $(".officalInfo"));
            },
            toMsgDetail(msg) {
                if (msg == '' || msg == null) {
                    return;
                }
                window.location.href = msg;
            }
        },
        components: {
            menuPage
        }
    }
</script>
<style rel="stylesheet/scss" lang="scss" scoped>
