        .seeCarHeader {
            width: 100%;
            box-sizing: border-box;
            padding: .2rem .4rem;
            background: #ffffff;
            border-bottom: 1px solid #ececec;
            .seeCarHeader_content {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .return {
                    width: 0.19rem;
                    height: 0.34rem;
                }
                .title {
                    font-size: 0.36rem;
                    color: #000000;
                }
                .menu {
                    width: 0.32rem;
                    height: 0.32rem;
                }
            }
        }
        .officalContent {
            overflow: hidden;
            .officalInfoItem {
                overflow: hidden;
                margin-top: .3rem;
                li {
                    position: relative;
                    margin-bottom: .3rem;
                    .hasImg {
                        width: 6.92rem;
                        margin: 0 auto;
                        .time {
                            text-align: center;
                            span {
                                line-height: .36rem;
                                padding: 0 .05rem;
                                font-size: .22rem;
                                background: #000000;
                                opacity: 0.1;
                                color: #ffffff;
                                border-radius: .08rem;
                            }
                        }
                        .offContent {
                            background: #ffffff;
                            margin-top: .3rem;
                            box-sizing: border-box;
                            padding: .3rem;
                            border: 1px solid #ececec;
                            -webkit-border-radius: 2rem;
                            -moz-border-radius: 2rem;
                            border-radius: .2rem;
                            line-height: 1;
                            .msg_title {
                                line-height: 1;
                                font-size: .3rem;
                                color: #000000;
                            }
                            .msg_info {
                                margin-top: .25rem;
                                font-size: .24rem;
                                line-height: .34rem;
                                color: #adadad;
                            }
                            .picHeight {
                                display: block;
                                width: 6.28rem;
                                height: 2.5rem;
                                margin-top: .3rem;
                            }
                            .to_msgDetail {
                                margin-top: .3rem;
                                padding: .3rem 0 0;
                                border-top: 1px solid #ececec;
                                display: flex;
                                justify-content: space-between;
                                a, i {
                                    font-size: .24rem;
                                    color: #000000;
                                }
                            }
                        }
                    }
                    .noImg {
                        width: 6.92rem;
                        margin: 0 auto;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        .time {
                            text-align: center;
                            span {
                                line-height: .36rem;
                                padding: 0 .05rem;
                                font-size: .22rem;
                                background: #000000;
                                opacity: 0.1;
                                color: #ffffff;
                                border-radius: .08rem;
                            }
                        }
                        .noImgContent {
                            background: #ffffff;
                            margin-top: .3rem;
                            box-sizing: border-box;
                            padding: .3rem;
                            border: 1px solid #ececec;
                            -webkit-border-radius: 2rem;
                            -moz-border-radius: 2rem;
                            border-radius: .2rem;
                            line-height: 1;
                            p:nth-child(1) {
                                font-size: .3rem;
                                color: #000000;
                            }
                            p:nth-child(2) {
                                margin-top: .25rem;
                                font-size: .24rem;
                                line-height: .34rem;
                                color: #adadad;
                            }
                        }
                    }
                }
            }
            .noCarImg {
                width: 100%;
                height: auto;
                margin-top: 0.1rem;
                text-align: center;
                background: #ffffff;
                img {
                    width: 4rem;
                    height: 4.22rem;
                    margin: 0.7rem 0;
                }
            }
            .cutPrice {
                margin: 0 0.24rem;
                .time {
                    text-align: center;
                    margin-top: 0.3rem;
                }
                .msg {
                    display: flex;
                    flex-direction: column;
                    margin-top: 0.24rem;
                    background: #ffffff;
                    -webkit-border-radius: 0 0 0.2rem 0.2rem;
                    -moz-border-radius: 0 0 0.2rem 0.2rem;
                    border-radius: 0 0 0.2rem 0.2rem;
                    .msg_content {
                        border-bottom: 1px solid #eeeeee;
                        p:nth-child(1) {
                            margin: 0.24rem 0 0.3rem 0.24rem;
                            font-size: 0.3rem;
                        }
                        p:nth-child(2) {
                            margin: 0 0 0.3rem 0.24rem;
                            font-size: 0.22rem;
                            color: #dddddd;
                        }
                    }
                    .msg_link {
                        height: 0.5rem;
                        display: flex;
                        align-items: center;
                        justify-content: flex-end;
