<template>
    <div class="myMessage">
        <div class="seeCarHeader">
            <div class="seeCarHeader_content">
                <span class="return" @click="back"><img src="../../../assets/return.png" width="100%" alt=""></span>
                <span class="title">我的消息</span>
                <span class="menu" @click="showMenu('搜索')"><img src="../../../assets/menu.png" width="100%"
                                                                alt=""></span>
            </div>
        </div>
        <div class="noCarImg" v-if="noCarImg"><img src="./noMsg.png" alt=""></div>
        <div class="myMessageList" v-else>
            <li class="msgItem" v-for="msgList in msgLists" @click="showOffical(msgList.msg_text,msgList.msg_type)">
                <!-- <router-link :to="{name: 'messageItem/'+ msgList.msg_type , params:{msgType: msgList.msg_type}}"> -->
                <dl>
                    <dt v-show="msgList.msg_text == '活动通知'"><img src="./offical-icon.png"/><span
                            v-show="!msgList.unreadTotal == 0" class="circle"></span></dt>
                    <dt v-show="msgList.msg_text == '降价通知'"><img src="./cutPrice-icon.png"/><span
                            v-show="!msgList.unreadTotal == 0" class="circle"></span></dt>
                    <dt v-show="msgList.msg_text == '售出通知'"><img src="./saled.png"/><span
                            v-show="!msgList.unreadTotal == 0" class="circle"></span></dt>
                    <dd>
                        <p class="msgTitle">
                            <span class="msgTitleRight">{{msgList.create_time}}</span>
                            <span>{{msgList.msg_text}}</span>
                        </p>
                        <p>{{msgList.msg}}</p>
                    </dd>
                </dl>
                <!-- </router-link> -->
            </li>
        </div>
        <menuPage ref="menuMark" @close="closeThis"></menuPage>
    </div>
</template>
<style rel="stylesheet/scss" lang="scss" scoped>
    .myMessage {
        position: absolute;
        background: #f5f5f5;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        overflow: auto;
        .seeCarHeader {
            width: 100%;
            box-sizing: border-box;
            padding: .2rem .4rem;
            background: #ffffff;
            border-bottom: 1px solid #ececec;
            .seeCarHeader_content {
                width: 100%;
                display: flex;
                justify-content: space-between;
                align-items: center;
                .return {
                    width: 0.19rem;
                    height: 0.34rem;
                }
                .title {
                    font-size: 0.36rem;
                    color: #000000;
                }
                .menu {
                    width: 0.32rem;
                    height: 0.32rem;
                }
            }
        }
        .noCarImg {
            width: 100%;
            height: 80%;
            display: flex;
            justify-content: center;
            align-items: center;
            img {
                width: 2.52rem;
                height: 1.87rem;
            }
        }
        .myMessageList {
            box-sizing: border-box;
            padding: 0 .4rem;
            background-color: #ffffff;
            overflow: hidden;
            .msgItem {
                box-sizing: border-box;
                padding: .3rem 0;
                &:not(:last-child) {
                    border-bottom: 1px solid #ececec;
                }
                dl {
                    display: flex;
                    flex-direction: row;
                    dt {
                        position: relative;
                        width: 0.8rem;
                        height: 0.8rem;
                        img {
                            width: 100%;
                            height: 100%;
                        }
                        span {
                            display: inline-block;
                            position: absolute;
                            box-sizing: border-box;
                            top: 0.05rem;
                            right: 0.1rem;
                            width: 0.12rem;
                            height: 0.12rem;
                            -webkit-border-radius: 100%;
                            -moz-border-radius: 100%;
                            border-radius: 100%;
                            background: #f00;
                        }
                    }
                    dd {
                        flex-direction: column;
                        width: 5.7rem;
                        margin-left: .2rem;
                        line-height: 1;
                        p:nth-child(1) {
                            span {
                                font-size: 0.3rem;
                                color: #333;
                            }
                            span.msgTitleRight {
                                float: right;
                                font-size: 0.24rem;
                                color: #666666;
                            }
                        }
                        p:nth-child(2) {
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            margin-top: .2rem;
                            font-size: .24rem;
                            color: #666666;
                        }
                    }
                }
            }
        }
    }

    /*.move-enter-active {*/
    /*transition: all .2s linear;*/
    /*transform: translate3d(0, 0, 0);*/
    /*}*/

    /*.move-leave-active {*/
    /*transition: all .2s linear;*/
    /*transform: translate3d(100%, 0, 0);*/
    /*}*/

    /*.move-enter, .move-leave {*/
    /*transform: translate3d(100%, 0, 0)*/
    /*}*/
</style>
<script>
    import menuPage from "../../public/nav.vue";
    import officalInfo from "./officalInfo/officalInfo.vue"

    export default {
        data() {
            return {
                showMenuPage: false,
                msgLists: '',
                msgItem: '',
                tokens: '',
                noCarImg: false,
                nativeHref: ''
            }
        },
        mounted() {
            var storage = window.localStorage;
            this.tokens = JSON.parse(storage.getItem("user")).token;
            var _this = this;
            $.ajax({
                type: 'POST',
                url: this.global.host + 'message/getCMessageTypeList',
                data: {
                    token: _this.tokens
                },
                success: function (response) {
                    if (response.resultCode == '10086') {
                        _this.errorAjax(response.resultMsg, _this)
                    } else {
                        if (response.resultCode == "0000") {
                            if (response.result.length == 0) {
                                _this.noCarImg = true;
                            } else {
                                _this.msgLists = response.result;
                                console.log(_this.msgLists);
                            }
                        } else {
                            alert(response.resultMsg);
                        }
                    }
                },
                error: function (response) {
                }
            })
        },
        methods: {
            back() {
                this.$router.back(-1);
            },
            closeThis() {
                this.showThis = false;
            },
            showMenu(msg) {
                this.$refs.menuMark.show(msg, $(".myMessage"));
            },
            showOffical(msg, msg_type) {
                var _this = this;
                if (msg == '活动通知') {
                    _this.$router.push('./messageOff/');
                    let infoId = 5;
                    $.ajax({
                        type: "POST",
                        url: this.global.host + 'message/updateCmsMsgPushUserReadStatus/' + infoId + '/?token=' + _this.tokens,
                        success: function (response) {
                            if (response.resultCode == 10086) {
                                _this.errorAjax(response.resultMsg, _this)
                            } else {
                                if (response.resultCode == '0000') {

                                } else {
                                    alert(response.resultMsg);
                                }
                            }
                        }
                    });

                }
                if (msg == '降价通知') {
                    _this.$router.push('./messageDown/');

                    let infoId = 6;
                    $.ajax({
                        type: "POST",
                        url: this.global.host + 'message/updateCmsMsgPushUserReadStatus/' + infoId + '/?token=' + _this.tokens,
                        success: function (response) {
                            if (response.resultCode == 10086) {
                                _this.errorAjax(response.resultMsg, _this)
                            } else {
                                if (response.resultCode == '0000') {
                                    console.log(response);
                                } else {
                                    alert(response.resultMsg);
                                }
                            }
                        }
                    });

                }
                if (msg == '售出通知') {
                    _this.$router.push('./messageSale/');

                    let infoId = 7;
                    $.ajax({
                        type: "POST",
                        url: this.global.host + 'message/updateCmsMsgPushUserReadStatus/' + infoId + '/?token=' + _this.tokens,
                        success: function (response) {
                            if (response.resultCode == 10086) {
                                _this.errorAjax(response.resultMsg, _this)
                            } else {
                                if (response.resultCode == '0000') {

                                } else {
                                    alert(response.resultMsg);
                                }
                            }
                        }
                    });

                }
            },
        },
        components: {
            menuPage,
        }
    }
</script>



